html {
  height: 100%;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100%;
  background: #ffffff;
  font-family: "PT Sans", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  color: #333333;
}

main {
  flex-grow: 1;
}

@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.page-header {
  display: flex;
  flex-direction: column;
}

.page-header-navigation {
  order: 1;
  width: 320px;
  position: relative;
}

.page-header__logo {
  order: 2;
  height: 56px;
  background-color: #ffffff;
  position: relative;
  display: flex;
  justify-content: center;
}

@media (min-width: 320px) {
  .page-header {
    background-color: #ffffff;
    width: 320px;
  }
  .page-header__menu-list {
    position: absolute;
    left: 0;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: #81b3d2;
    list-style: none;
    padding: 0;
    margin: 0;
  }
  .page-header-navigation--nojs .page-header__menu-list {
    position: relative;
  }
  .page-header__menu-button {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
    display: block;
    width: 56px;
    height: 56px;
    background-color: transparent;
    cursor: pointer;
    border: none;
  }
  .page-header-navigation--opened .page-header__menu-button::before {
    display: none;
    content: "";
    position: absolute;
    top: 15px;
    left: 17px;
    width: 22.2px;
    height: 22.2px;
    background-image: url("../img/stack.svg#cross-icon");
  }
  .page-header-navigation--opened .page-header__menu-button:hover::before {
    opacity: 0.6;
  }
  .page-header-navigation--opened .page-header__menu-button:active::before {
    opacity: 0.3;
  }
  .page-header-navigation--closed .page-header__menu-button {
    top: 0;
    right: 0;
  }
  .page-header-navigation--closed .page-header__menu-button::before {
    content: "";
    position: absolute;
    top: 16.9px;
    left: 16.55px;
    width: 22.9px;
    height: 22.2px;
    background-image: url("../img/stack.svg#burger-icon");
  }
  .page-header-navigation--closed .page-header__menu-button:hover::before {
    opacity: 0.6;
  }
  .page-header-navigation--closed .page-header__menu-button:active::before {
    opacity: 0.3;
  }
  .page-header-navigation--closed .page-header__menu-list {
    display: none;
  }
  .page-header__menu-item {
    display: flex;
    min-width: 320px;
    justify-content: center;
    align-items: center;
    min-height: 55px;
    margin-bottom: 1px;
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
  .page-header__menu-item:last-child {
    box-shadow: 0 0 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
    margin-bottom: 0;
  }
  .page-header__menu-item:last-child::after {
    position: absolute;
    bottom: 0;
    content: "";
    min-width: 320px;
    height: 7px;
    background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
  }
  .page-header__menu-item--condition:hover {
    background-color: #669ec0;
  }
  .page-header__menu-item--condition:active {
    background-color: #5496bd;
  }
  .page-header__menu-item--condition:active .page-header__menu-link {
    opacity: 0.3;
  }
  .page-header__menu-link {
    text-decoration: none;
    color: #ffffff;
    font-family: "PT Sans", sans-serif;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
  }
}
@media (min-width: 768px) {
  .page-header__menu-button {
    display: none;
  }
  .page-header-navigation--nojs .page-header__menu-list {
    position: static;
  }
  .page-header__menu-item:last-child::after {
    display: none;
  }
  .page-header {
    width: 768px;
  }
  .page-header__logo {
    position: absolute;
    left: 50%;
    margin-left: -92.5px;
  }
  .page-header-navigation {
    position: static;
    width: 768px;
  }
  .page-header__menu-list {
    position: static;
    display: flex;
    align-items: center;
    flex-direction: row;
    justify-content: start;
    background-color: #ffffff;
    flex-wrap: wrap;
  }
  .page-header__menu-item {
    min-width: 153px;
    min-height: 56px;
    box-shadow: none;
  }
  .page-header__menu-item--indent {
    margin-right: 153px;
  }
  .page-header__menu-link {
    color: #000000;
    font-size: 14px;
    line-height: 21px;
    padding: 15px;
  }
  .page-header__menu-item--condition:hover {
    background-color: #ffffff;
  }
  .page-header__menu-item--condition:hover .page-header__menu-link {
    color: #81b3d2;
  }
  .page-header__menu-item--condition:active .page-header__menu-link {
    color: #000000;
    opacity: 0.3;
  }
  .page-header__menu-item--condition .page-header__menu-link--active {
    color: #766357;
  }
  .page-header-navigation__burger-menu-label {
    display: none;
  }
}
@media (min-width: 1200px) {
  .page-header {
    width: 1200px;
  }
  .page-header-navigation {
    width: 1200px;
  }
  .page-header__menu-item {
    width: 240px;
    height: 56px;
    box-shadow: none;
  }
  .page-header__menu-item--indent {
    margin-right: 240px;
  }
}
